.head {
  display:flex;
  width: 100%;
  height: 40px;
  justify-content: center;
  align-items: center;
  font-family: 'Handlee', cursive;
  
  font-size: 30px;
  
}
.head:hover{
  text-decoration:none;
}
.sec{
  margin-top: 5%;
  margin-left: 15%;
  margin-right: 15%;
  background-color: rgb(141, 219, 141);
  border-radius: 55px;
  display: flex;
  justify-content: center;
  justify-content: space-between;
  padding-left: 5%;
  padding-right: 10%;
  

  
  
}

.dead{
  display: flex;
  justify-items: center;
  display: grid;
  margin-top: 5px;
  margin-bottom: 5px;

  
}
.foot{
  margin-top: 80%;
  width: 100%;
  height: 350px;
  background-color: rgb(28, 38, 54);
  display: inline-flex;
  color: aliceblue;
  font-weight: normal;
  font-family: 'Handlee', cursive;
  display: flex;
  justify-content: space-around;
  transition: width 2s, height 4s;
}
h4:hover{
  text-decoration-line:underline;
  text-decoration-color: rgb(141, 219, 141);
  transition-timing-function: linear;
}

h1{
  display: flex;
  justify-content: center;
  font-family: 'Handlee', cursive;
}
h5{
  font-family: 'Handlee', cursive;

}
.copyright{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(28, 38, 54);
  color: aliceblue;

}
.App{
  background-color: rgb(255, 255, 255);
}

.imge{
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 5px;
   margin-bottom: 5px;
   
} 
p{
  font-family: 'Handlee', cursive;
  font-size: 35px;
}